.nav-side {
  font-size: 1.4rem;
  list-style: none;
  margin-top: 3.5rem;
  &__item {
    position: relative;
    &:not(:last-child) {
      margin-bottom: 0.5rem;
    }
  }
  &__item::before {
    content: "";
    position: absolute;
    width: 0.3rem;
    height: 100%;
    top: 0;
    left: 0;
    transform: scaleY(0);
    background-color: var(--color-primary-light);
    transition: transform 1s, width 0.4s cubic-bezier(1, 0, 0, 1) 0.2s,
      background-color 0.1s;
  }
  &__item:hover::before,
  &__item--active::before {
    width: 100%;
    transform: scaleY(1);
  }
  &__item:active::before {
    background-color: var(--color-primary-light);
  }

  &__link:link,
  &__link:visited {
    text-decoration: none;
    text-transform: uppercase;
    display: inline-block;
    padding: 1.5rem 3rem;
    color: #f2f4f7;
    position: relative;
    z-index: 10;
    display: flex;
    align-items: center;
  }
  &__icon {
    height: 1.75rem;
    width: 1.75rem;
    fill: #f2f4f7;
    margin-right: 2rem;
  }
}
.legal {
  font-size: 1.75rem;
  color: var(--color-grey-light-4);
}
