.section-signin {
  font-family: "Open Sans", sans-serif;
  line-height: 1.6;
  background-color: var(--color-primary);
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.signin {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50%;
  padding-left: 2rem;
  padding-right: 2rem;

  &__heading {
    font-size: 2.4rem;
    font-weight: 400;
    color: var(--color-grey-light-1);
    margin-bottom: 2.75rem;
  }

  &__navigation {
    list-style: none;
    margin-bottom: 2.75rem;
  }
  &__item {
    display: inline-block;

    &:not(:last-child) {
      margin-right: 1.5rem;
    }
  }
  &__link {
    &:link,
    &:visited {
      font-size: 1.8rem;
      text-decoration: none;
      color: var(--color-grey-light-4);
      text-decoration: none;
      display: inline-block;
      transition: all 0.2s;
    }
    &:hover,
    &:active {
      color: #ffcc01;
      border-bottom: 1px solid #ffcc01;
    }
    &--active {
      color: #ffcc01 !important;
      border-bottom: 1px solid #ffcc01;
    }
  }

  &__box {
    background-color: #fff;
    box-shadow: 0 2rem 6rem rgba(0, 0, 0, 0.3);
    padding: 4rem;
    width: 80%;
    border-radius: 3px;
  }
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  label {
    display: block;
    font-size: 1.5rem;
    line-height: 1.6;
    font-weight: 600;
    color: var(--color-grey-dark-1);
  }
  button {
    padding: 1.5rem 3rem;
    font-size: 1.5rem;
    background: var(--color-primary);
    color: white;
    border: none;
    border-radius: 4px;

    &:hover {
      cursor: pointer;
    }
  }
  input[type="email"],
  input[type="password"] {
    display: block;
    font-size: 1.5rem;
    padding: 0.75rem 1.25rem;
    margin-top: 0.6rem; /* Add a top margin */
    margin-bottom: 1.6rem;
    width: 100%;
    border: 1px solid #ccc;
    border-radius: 4px;
    color: var(--color-primary);

    &:focus {
      outline: none;
    }
  }
}

.forgot-pasword {
  &:link,
  &:visited {
    text-decoration: none;
    color: #f2f2f2;
    display: inline-block;
    padding: 0.75rem 1.25rem;
    margin-top: 2rem;
    font-size: 1.4rem;
  }
}
